.sw-card{max-width:960px;margin:0 auto 40px auto;position:relative;color:#52667a}.sw-card__context-menu{position:absolute;top:30px;right:30px}.sw-card.sw-card--grid .sw-card__content{display:grid;padding:0}.sw-card.sw-card--grid .sw-card__content .sw-grid{border-top:none}.sw-card.sw-card--hero .sw-card__content{background:none;border:none;text-align:center}.sw-card.sw-card--hero .sw-card__content h3{font-size:30px}.sw-card.sw-card--large{max-width:1330px}.sw-card.sw-card--large .sw-card__title,.sw-card.sw-card--large .sw-card__subtitle{width:auto;position:relative;top:0;left:0;text-align:left}.sw-card .sw-card__title,.sw-card .sw-card__subtitle{margin-bottom:12px;color:#52667a}@media screen and (min-width: 1580px){.sw-card .sw-card__title,.sw-card .sw-card__subtitle{width:200px;position:absolute;left:-240px;text-align:right}}.sw-card .sw-card__title{font-size:18px;line-height:24px}@media screen and (min-width: 1580px){.sw-card .sw-card__title{top:0}}.sw-card .sw-card__subtitle{font-size:14px;line-height:20px}@media screen and (min-width: 1580px){.sw-card .sw-card__subtitle{top:40px}}.sw-card .sw-card__toolbar{padding:30px;background-color:#f0f2f5;border:1px solid #d1d9e0}.sw-card.has--header .sw-card__content{border-top:none;border-radius:0 0 4px 4px}.sw-card .sw-card__content{padding:30px;background:#fff;background-clip:padding-box;border-radius:4px;border:1px solid #d1d9e0;position:relative}@media screen and (max-width: 800px){.sw-card .sw-card__content{padding:15px}}.sw-card .sw-card__content h1,.sw-card .sw-card__content h2,.sw-card .sw-card__content h3,.sw-card .sw-card__content h4,.sw-card .sw-card__content h5,.sw-card .sw-card__content h6{font-weight:normal}.sw-card .sw-card__content h1{font-size:24px}.sw-card .sw-card__content h2{font-size:22px}.sw-card .sw-card__content h3{font-size:20px}.sw-card .sw-card__content h4,.sw-card .sw-card__content h5,.sw-card .sw-card__content h6{font-size:18px}.sw-card .sw-card__content a.sw-card__quick-link{display:grid;grid-auto-flow:column;grid-column-gap:6px;align-items:center;text-decoration:none;color:#189eff;font-size:14px}.sw-card .sw-card__content a.sw-card__quick-link:hover{color:#1596ff}.sw-card .sw-card__content .sw-tabs{border-width:0 0 1px 0;border-bottom-right-radius:0;border-bottom-left-radius:0;margin-bottom:0}.sw-card .sw-card__content .sw-tabs .sw-tabs__container{position:relative;top:-5px}.sw-card .sw-card__content .sw-tabs-item{margin:0;padding-top:5px;line-height:44px;border-bottom-left-radius:0;border-bottom-right-radius:0}.sw-card .sw-card__content .sw-tabs-item:hover{border-bottom-color:#d1d9e0}.sw-card .sw-card__content .sw-tabs-item:focus{background-color:#f0f2f5;border-bottom-color:#d1d9e0}.sw-card .sw-card__content .sw-tabs-item:active{border-bottom-color:#fff;background-color:#fff}.sw-card .sw-card__content .sw-tabs-item.router-link-active{background-color:#fff;border-bottom-color:#fff}.sw-card.sw-card--tabs .sw-card__content{border-radius:0 0 4px 4px;border-top:none}.sw-card.sw-card--tabs>.sw-tabs{border:1px solid #d1d9e0;border-radius:4px 4px 0 0;border-bottom:none;margin-bottom:0}.sw-card.sw-card--tabs>.sw-tabs .sw-tabs__content{background-color:#f9fafb}.sw-card.sw-card--tabs>.sw-tabs .sw-tabs__custom-Content{padding:0}.sw-card.sw-card--tabs>.sw-tabs .sw-tabs__arrow{top:28px;padding:0 12px 0 12px}.sw-card.sw-card--tabs>.sw-tabs.sw-tabs--scrollable{padding:0 32px 0 32px}.sw-card.sw-card--tabs>.sw-tabs .sw-tabs-item{padding-top:19px;padding-bottom:15px;font-weight:normal}.sw-card.sw-card--tabs>.sw-tabs .sw-tabs-item.sw-tabs-item--active{font-weight:600}