.sw-field.sw-field--select .sw-block-field__block{width:100%;position:relative}.sw-field.sw-field--select .sw-block-field__block .sw-field--select__options{display:block;position:absolute;top:calc(50% - 10px);right:12px;text-align:center;color:#52667a;pointer-events:none}.sw-field.sw-field--select.sw-field--select-aside{display:inline-grid;grid-template-columns:auto 1fr;grid-column-gap:8px;align-items:center}.sw-field.sw-field--select.sw-field--select-aside .sw-field__label{text-align:right;margin-bottom:0}.sw-field.sw-field--select .sw-field--select__placeholder-option{color:#94a6b7}.sw-field.sw-field--select select{padding-right:30px}.sw-field.sw-field--select select option{color:#52667a;background-color:#fff}.sw-field.sw-field--select select option:disabled{color:#d1d9e0}.sw-field.sw-field--select select.is--placeholder{color:#94a6b7}.sw-field.sw-field--select .sw-field--select__load-placeholder{height:45px;border-radius:4px;border:1px solid #d1d9e0;background:#fff;-webkit-appearance:none;-moz-appearance:none;appearance:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;position:relative}.sw-field.sw-field--select .sw-field--select__loader{background:none}