.sw-icon{width:24px;height:24px;display:inline-block;vertical-align:middle;line-height:0}.sw-icon>svg{width:100%;height:100%;vertical-align:middle}.sw-icon.sw-icon--fill>svg{fill:currentColor}.sw-icon.sw-icon--fill>svg path,.sw-icon.sw-icon--fill>svg use{fill:currentColor}.sw-icon.sw-icon--small{width:16px;height:16px}.sw-icon.sw-icon--large{width:32px;height:32px}