.sw-select{position:relative}.sw-select .sw-block-field__block{transition:all ease-in-out .2s;background-color:#fff;position:relative;overflow:visible}.sw-select .sw-select__selection{position:relative;padding:0 8px;border:none;font-size:14px;font-family:"Source Sans Pro","Helvetica Neue","Helvetica","Arial",sans-serif;line-height:22px;color:#52667a;outline:none;-webkit-appearance:none;-moz-appearance:none}.sw-select .sw-select__selection-indicators{position:absolute;display:flex;gap:8px;top:50%;right:16px;transform:translate(0, -50%);z-index:1}.sw-select .sw-select__selection-indicators .sw-loader{position:static;width:16px;height:16px;margin:0}.sw-select .sw-select__selection-indicators .sw-loader .sw-loader__container{transform:none;left:0;top:0}.sw-select .sw-select__select-indicator-hitbox{background-color:transparent;border:0 solid transparent;color:#52667a;padding:0 4px;cursor:pointer}.sw-select .sw-select__select-indicator-hitbox .sw-select__select-indicator{display:block}.sw-select .sw-select__select-indicator-hitbox:focus .sw-select__select-indicator-clear{opacity:1;pointer-events:all;cursor:pointer}.sw-select .sw-select__select-indicator{flex-shrink:0;cursor:pointer}.sw-select .sw-select__select-indicator-clear{transition:.1s opacity ease;opacity:0;pointer-events:none}.sw-select:hover .sw-select__select-indicator-clear,.sw-select.has--focus .sw-select__select-indicator-clear{opacity:1;pointer-events:all;cursor:pointer}.sw-select.sw-field--medium .sw-select__selection{padding:4px 6px 0}.sw-select.sw-field--small .sw-select__selection{padding:4px 6px 0}.sw-select.is--disabled .sw-block-field__block{background-color:#f0f2f5}.sw-select.is--disabled .sw-label{background-color:#f0f2f5}.sw-select.is--disabled input{background-color:#f0f2f5}.sw-select-result-list-fade-down-enter-active,.sw-select-result-list-fade-down-leave-active{transition:all ease-in-out .2s;transform:translateY(0)}.sw-select-result-list-fade-down-enter,.sw-select-result-list-fade-down-leave-to{transform:translateY(-10px);opacity:0}