.sw-rating-stars{position:relative;height:22px;margin:2px}.sw-rating-stars__star{margin-right:1px}.sw-rating-stars__star:last-child{margin-right:0}.sw-rating-stars__value{position:absolute;left:0;top:0;font-size:0}.sw-rating-stars__value .icon--default-review-star-full{color:#ffcd00}.sw-rating-stars__placeholder{position:absolute;left:0;top:0;font-size:0}.sw-rating-stars__placeholder .icon--default-review-star-full{color:#d1d9e0}