.sw-context-button{display:inline-block;position:relative;color:#52667a}.sw-context-button.is--disabled .sw-context-button__button{color:#8599ad;cursor:initial}.sw-context-button.is--disabled .sw-context-button__button:hover,.sw-context-button.is--disabled .sw-context-button__button.is--active{border:none}.sw-context-button .sw-context-button__button{color:#52667a;background:0 none;border:1px solid transparent;border-radius:4px;cursor:pointer;height:24px;line-height:20px;padding:0 8px;outline:none}.sw-context-button .sw-context-button__button:hover,.sw-context-button .sw-context-button__button.is--active{border-color:#d1d9e0}.sw-context-button__menu-position{position:absolute;left:50%;top:100%;transform:translate(-35px, 10px)}.sw-popover__wrapper.sw-context-button__menu-popover.--placement-bottom-outside .sw-context-menu{transform:translate(0, 13px)}.sw-popover__wrapper.sw-context-button__menu-popover.--placement-bottom-outside .sw-context-menu .sw-context-menu__content::before{top:auto;bottom:-5px;transform:rotate(225deg)}.sw-popover__wrapper.sw-context-button__menu-popover.--placement-right-outside .sw-context-menu{transform:translate(calc(-100% + 66px), 0)}.sw-popover__wrapper.sw-context-button__menu-popover.--placement-right-outside .sw-context-menu .sw-context-menu__content::before{left:unset;right:28px}.sw-popover__wrapper.sw-context-button__menu-popover.--placement-right-outside.--placement-bottom-outside .sw-context-menu{transform:translate(calc(-100% + 66px), 13px)}