.sw-field{width:100%;margin-bottom:32px}.sw-field .sw-field__label{display:flex;line-height:16px;font-size:14px;margin-bottom:8px;color:#52667a}.sw-field .sw-field__label label{flex-grow:1}.sw-field .sw-field__label .is--required::after{content:"*";color:#189eff}.sw-field .sw-field__inheritance-icon{margin-right:8px}.sw-field .sw-field__button-restore{color:#52667a;padding:0 8px;border:none;background:none;outline:none;-moz-appearance:none;-webkit-appearance:none;cursor:pointer}.sw-field .sw-field__help-text{align-self:center}.sw-field.is--inherited .sw-field__label{color:#7363e5}.sw-field.has--error{margin-bottom:12px}.sw-field__hint{margin-top:4px;font-size:12px;color:#b3bfcc;display:flex;align-items:center;gap:8px}