.sw-progress-bar{width:100%;height:8px}.sw-progress-bar .sw-progress-bar__total{width:100%;height:100%;background-color:#c2ccd6;border-radius:4px}.sw-progress-bar .sw-progress-bar__value{transition:1s width linear;height:100%;background-color:#189eff;border-radius:4px}.sw-progress-bar .sw-progress-bar__value--no-transition{transition:0s width linear}