.sw-loader{width:100%;height:100%;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;z-index:400;background:rgba(255,255,255,.8)}.sw-loader .sw-loader__container{display:grid;grid-auto-columns:auto;text-align:center;position:relative;top:50%;left:50%;transform:translate(-50%, -50%)}.sw-loader .sw-loader-element div{position:absolute;width:100%;height:100%;border-width:4px;border-style:solid;border-radius:50%;border-color:#189eff transparent transparent transparent;-webkit-animation:sw-loader-rotator 1.4s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:sw-loader-rotator 1.4s cubic-bezier(0.5, 0, 0.5, 1) infinite}.sw-loader .sw-loader-element div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.sw-loader .sw-loader-element div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.sw-loader .sw-loader-element div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes sw-loader-rotator{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes sw-loader-rotator{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}