.sw-tabs-item{transition:.3s all ease-in-out;padding:10px 17.5px;border-bottom:2px solid #d1d9e0;text-decoration:none;white-space:nowrap;color:#52667a;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sw-tabs-item .sw-tabs-item__error-badge{position:relative;bottom:0;right:-5px;color:#e86982;transform:translateY(-12%)}.sw-tabs-item.sw-tabs-item--active{color:#14191f}.sw-tabs-item.sw-tabs-item--active .sw-tabs-item__error-badge{color:#e34967}.sw-tabs-item.sw-tabs-item--has-error{color:#e86982}.sw-tabs-item.sw-tabs-item--has-error.sw-tabs-item--active{color:#de294c}.sw-tabs-item.sw-tabs-item--is-disabled{color:#a3b3c2;pointer-events:none}.sw-tabs-item:disabled{color:#a3b3c2;cursor:not-allowed}.sw-tabs--vertical .sw-tabs-item{padding:10px;border-bottom:0 solid #d1d9e0;border-left:2px solid #d1d9e0}.sw-tabs--vertical.sw-tabs--align-right .sw-tabs-item{border-left:0;border-right:2px solid #d1d9e0}