.sw-context-menu-item{display:block;position:relative;border-radius:4px;margin:0;line-height:18px;padding:4px 14px;text-decoration:none;color:#52667a;outline:none;cursor:pointer}.sw-context-menu-item.sw-context-menu-item--icon{display:grid;grid-template-columns:16px auto;align-items:center;justify-items:stretch;justify-content:stretch;grid-gap:12px}.sw-context-menu-item:hover{background:#e4f4ff;color:#189eff}.sw-context-menu-item:hover .sw-icon{color:#189eff}.sw-context-menu-item.sw-context-menu-item--danger{color:#de294c}.sw-context-menu-item.sw-context-menu-item--danger .sw-icon{color:#de294c}.sw-context-menu-item.sw-context-menu-item--danger:hover{background:#fbe3e7}.sw-context-menu-item.sw-context-menu-item--danger:hover.is--disabled{color:#de294c}.sw-context-menu-item.sw-context-menu-item--success{color:#37d046}.sw-context-menu-item.sw-context-menu-item--success .sw-icon{color:#37d046}.sw-context-menu-item.sw-context-menu-item--success:hover{background:#e4f9e6}.sw-context-menu-item.sw-context-menu-item--success:hover.is--disabled{color:#37d046}.sw-context-menu-item.sw-context-menu-item--warning{color:#ff9800}.sw-context-menu-item.sw-context-menu-item--warning .sw-icon{color:#ff9800}.sw-context-menu-item.sw-context-menu-item--warning:hover{background:#ffcc80}.sw-context-menu-item.sw-context-menu-item--warning:hover.is--disabled{color:#ff9800}.sw-context-menu-item.sw-context-menu-item--headline{text-align:center;cursor:default}.sw-context-menu-item.sw-context-menu-item--headline:hover{color:#52667a;background:0 none}.sw-context-menu-item.is--disabled{opacity:.5;cursor:not-allowed;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sw-context-menu-item.is--disabled:hover{color:#52667a;background:0 none}.sw-context-menu-item.is--disabled .sw-icon{color:#52667a}.sw-context-menu-item>.sw-icon{color:#52667a}