.sw-color-badge{display:inline-block;height:8px;width:8px;margin:0 0 1px 10px;border-radius:2px;background-color:#d1d9e0}.sw-color-badge.is--rounded{border-radius:100%}.sw-color-badge.is--warning{background-color:#ff9800}.sw-color-badge.is--error,.sw-color-badge.is--danger{background-color:#de294c}.sw-color-badge.is--success{background-color:#37d046}.sw-color-badge.is--info{background-color:#189eff}