.sw-context-menu{position:relative;z-index:1100}.sw-context-menu.is--top-align .sw-context-menu__content{margin-top:calc(-100% - 30px)}.sw-context-menu.is--top-align .sw-context-menu__content::before{transform:rotate(-135deg);bottom:-5px;top:unset}.sw-context-menu .sw-context-menu__content{padding:8px;background:#fff;box-shadow:0 3px 6px 0 rgba(120,138,155,.3);border:1px solid #d1d9e0;border-radius:4px;text-align:left;font-size:14px;color:#52667a;position:relative}.sw-context-menu .sw-context-menu__content::before{content:"";position:absolute;top:-5px;left:28px;height:8px;width:8px;transform:rotate(45deg);background:#fff;border-top:1px solid #d1d9e0;border-left:1px solid #d1d9e0}.sw-context-menu .sw-context-menu__content .currency-symbol{font-weight:bold;font-size:18px}.sw-context-menu .sw-context-menu__content .sw-context-menu__group{padding-top:10px;margin-top:10px;border-top:1px solid #d1d9e0}.sw-context-menu .sw-context-menu__content .sw-context-menu__group:first-child{border-top:0 none;padding-top:5px;margin-top:0}