.sw-contextual-field input:-webkit-autofill{-webkit-box-shadow:0 0 0 1000px #fff inset}.sw-contextual-field .sw-block-field__block{display:flex}.sw-contextual-field .sw-field__addition{display:flex;flex-shrink:0;justify-content:center;align-items:center;min-width:50px;background:#f9fafb;border-left:1px solid #d1d9e0;border-right:none;line-height:22px;padding:12px 15px;font-size:14px;color:#52667a;transition:border-color .3s ease-out}.sw-contextual-field .sw-field__addition .sw-icon{width:16px;height:16px}.sw-contextual-field .sw-field__addition.is--prefix{border-right:1px solid #d1d9e0;border-left:none}.sw-contextual-field.is--disabled .sw-field__addition{background:#f0f2f5}.sw-contextual-field.sw-field--small .sw-field__addition{padding:4px 16px}.sw-contextual-field.sw-field--medium .sw-field__addition{padding:8px 16px}