.sw-modal{position:fixed;top:0;right:0;bottom:0;left:0;height:100%;width:100%;overflow-x:hidden;overflow-y:auto;background-color:rgba(0,0,0,.4);z-index:1000;outline:none;display:flex;flex-direction:row;justify-content:center;align-items:center;padding:0 32px}.sw-modal .sw-modal__dialog{background-color:#fff;border-radius:4px;color:#52667a;max-height:calc(100vh - 64px);min-height:200px;width:100%;outline:none;display:flex;flex-direction:column}.sw-modal.sw-modal--default .sw-modal__dialog{max-width:700px}.sw-modal.sw-modal--large .sw-modal__dialog{max-width:900px}.sw-modal.sw-modal--small .sw-modal__dialog{max-width:500px}@media screen and (max-width: 500px){.sw-modal{padding:0;overflow-y:hidden}.sw-modal .sw-modal__dialog{border-radius:0;display:grid;height:100%;width:100%;max-height:100%;grid-template-rows:auto 1fr auto}.sw-modal .sw-modal__body{overflow-x:hidden;overflow-y:auto}}.sw-modal.sw-modal--full .sw-modal__dialog{max-width:100%;margin:20px;height:calc(100vh - 64px)}.sw-modal .sw-modal__header{border-bottom:1px solid #d1d9e0;display:flex;flex-shrink:0;flex-direction:row;justify-content:space-between;align-items:center;position:relative;padding:0 16px 0 32px;height:64px}.sw-modal .sw-modal__title{margin:0;font-weight:normal;font-size:18px;white-space:nowrap;text-overflow:ellipsis;overflow:hidden}.sw-modal .sw-modal__close{background:0 none;color:#5c738a;cursor:pointer;outline:none;flex-shrink:0;border:0 none;border-radius:50%;margin-left:auto;height:40px;width:40px}.sw-modal .sw-modal__close:hover{color:#52667a}.sw-modal .sw-modal__close:focus{background-color:#e0e6eb;color:#52667a}.sw-modal .sw-modal__body{overflow-y:auto;padding:20px 30px;position:relative;word-break:break-word;flex-grow:1}.sw-modal .sw-modal__body .sw-data-grid .sw-data-grid__bulk{padding-left:126px;padding-top:22px}.sw-modal .sw-modal__footer{border-radius:0 0 4px 4px;background-color:#f0f2f5;display:grid;grid-auto-columns:-webkit-min-content;grid-auto-columns:min-content;grid-auto-flow:column;grid-column-gap:8px;justify-content:end;align-items:center;flex-shrink:0;height:72px;padding:0 32px}.sw-modal .sw-loader{z-index:1001}.sw-modal-fade-enter-active,.sw-modal-fade-leave-active{transition:opacity .4s cubic-bezier(0.68, -0.55, 0.26, 1.55)}.sw-modal-fade-enter-active .sw-modal__dialog,.sw-modal-fade-leave-active .sw-modal__dialog{transition:transform .4s cubic-bezier(0.68, -0.55, 0.26, 1.55);transform:scale(1)}.sw-modal-fade-enter,.sw-modal-fade-leave-to{opacity:0}.sw-modal-fade-enter .sw-modal__dialog,.sw-modal-fade-leave-to .sw-modal__dialog{transform:scale(0.8)}