.sw-page{width:100%;height:100%;position:relative;display:grid;grid-template-rows:1fr}.sw-page.has--head-area{grid-template-rows:auto 1fr}.sw-page .sw-page__head-area{position:relative;display:grid;grid-column-gap:8px;grid-template-columns:[head-start back-btn-start] 1fr [back-btn-end header-content-start] minmax(0, 800px) [header-content-end] 1fr [smart-bar-end];align-items:center;background:#fff;border-bottom:2px solid #d1d9e0}.sw-page .sw-page__search-bar{grid-row:1;grid-column:header-content-start/header-content-end;z-index:800}@media screen and (max-width: 500px){.sw-page .sw-page__search-bar{grid-column:head-start/smart-bar-end}}.sw-page .sw-page__top-bar-actions{grid-row:1;grid-column:header-content-end/smart-bar-end;padding-right:32px;display:flex;align-content:flex-end;justify-content:flex-end}.sw-page .sw-page__smart-bar-divider{position:absolute;top:80px;width:100%;height:1px;background-color:#d1d9e0}.sw-page .sw-page__back-btn-container{grid-column:back-btn-start/back-btn-end;grid-row:2}.sw-page .smart-bar__back-btn{width:54px;height:24px;line-height:24px;display:grid;grid-template-columns:30px 24px;align-items:center;margin-left:20px}.sw-page .smart-bar__back-btn .sw-icon.icons--small-arrow-large-left{line-height:24px;margin:auto}.sw-page .smart-bar__content{min-height:70px;max-width:800px;padding:15px 0;display:flex;justify-content:space-between;align-items:center;grid-column:header-content-start/header-content-end;grid-row:2}@media screen and (max-width: 1360px){.sw-page .smart-bar__content{padding:15px}}.sw-page .smart-bar__header{flex:1;min-width:0}.sw-page .smart-bar__header h2{color:#52667a;font-size:18px;font-weight:600;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;margin-bottom:0}.sw-page .smart-bar__header h2 .sw-icon{position:relative;top:-2px}.sw-page .smart-bar__actions{display:flex;flex-direction:row;text-align:right;white-space:nowrap}.sw-page .smart-bar__actions .sw-button,.sw-page .smart-bar__actions .sw-button-group,.sw-page .smart-bar__actions .sw-tooltip--wrapper{margin-left:8px}.sw-page .smart-bar__actions .sw-button:first-child,.sw-page .smart-bar__actions .sw-button-group:first-child,.sw-page .smart-bar__actions .sw-tooltip--wrapper:first-child{margin-left:0}.sw-page .smart-bar__actions .sw-context-button__menu-position{margin-right:0}.sw-page .sw-page__smart-bar-amount{font-weight:normal}.sw-page .sw-page__content{position:relative;height:100%}.sw-page .sw-page__content.has--side-content{display:grid;grid-template-columns:440px 1fr auto}.sw-page .sw-page__content.has--side-bar{display:grid;grid-template-columns:1fr auto}.sw-page .sw-page__content .content__wrapper{position:relative;width:100%;height:100%}.sw-page .sw-page__main-content,.sw-page .sw-page__side-content{position:relative;height:100%}.sw-page .sw-page__main-content-inner,.sw-page .sw-page__side-content-inner{position:absolute;overflow:auto;height:100%;width:100%}.sw-page .sw-page__side-content{border-right:1px solid #d1d9e0}