.sw-tabs{position:relative}.sw-tabs .sw-tabs__content{position:relative;display:flex;overflow:auto;scroll-behavior:smooth}.sw-tabs .sw-tabs__content::-webkit-scrollbar{display:none}.sw-tabs .sw-tabs__content::before{content:"";display:block;position:absolute;bottom:0;width:100%;height:2px;background-color:#d1d9e0;pointer-events:none}.sw-tabs .sw-tabs__content .sw-tooltip--wrapper{display:flex}.sw-tabs .sw-tabs__slider{transform-origin:top left;transition:.2s all ease-in-out;position:absolute;bottom:0;height:2px;background-color:#189eff}.sw-tabs .sw-tabs__slider.has--error{background-color:#de294c}.sw-tabs .sw-tabs__arrow{transition:.3s all ease-in-out;position:absolute;left:0;top:21px;transform:translate(0, -50%);border:none;background-color:transparent}.sw-tabs .sw-tabs__arrow.sw-tabs__arrow--right{left:auto;right:0}.sw-tabs .sw-tabs__arrow.sw-tabs__arrow--disabled{cursor:default !important;opacity:.25}.sw-tabs .sw-tabs__arrow .sw-icon{transition:.3s all ease}.sw-tabs .sw-tabs__arrow:focus{outline:none}.sw-tabs .sw-tabs__arrow:hover{outline:none;cursor:pointer}.sw-tabs .sw-tabs__arrow:hover .sw-icon{color:#189eff}.sw-tabs .sw-tabs__custom-content{padding:20px 0}.sw-tabs.sw-tabs--vertical .sw-tabs__content{flex-direction:column}.sw-tabs.sw-tabs--vertical .sw-tabs__content .sw-tabs__slider{bottom:auto;top:0;left:2px}.sw-tabs.sw-tabs--vertical .sw-tabs__content::before{display:none}.sw-tabs.sw-tabs--vertical.sw-tabs--align-right .sw-tabs__content .sw-tabs-item{text-align:right}.sw-tabs.sw-tabs--vertical.sw-tabs--align-right .sw-tabs__content .sw-tabs__slider{transform-origin:bottom right;left:auto;right:0;top:-2px}.sw-tabs.sw-tabs--small{max-width:800px;margin:0 auto 15px auto}.sw-tabs.sw-tabs--scrollable{padding:0 20px}.sw-tabs.sw-tabs--scrollbar-active .sw-tabs__content::before{display:none}