.sw-code-editor{margin-bottom:22px}.sw-code-editor .sw-code-editor__label{display:block;line-height:16px;font-size:14px;margin-bottom:8px;color:#52667a;font-weight:normal;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sw-code-editor .sw-code-editor__editor{border:1px solid #d1d9e0;height:260px}.sw-code-editor__sanitize-bar{position:relative;width:100%;overflow:visible}.sw-code-editor__sanitize-info,.sw-code-editor__sanitized-hint{display:flex;font-size:.875rem;padding:.25rem 0;position:absolute;width:100%;opacity:0;transition:opacity .16s ease}.sw-code-editor__sanitize-info--visible,.sw-code-editor__sanitized-hint--visible{opacity:1}.sw-code-editor__sanitize-text{margin-top:.375rem}