.sw-avatar{display:inline-block;width:40px;height:40px;border-radius:100%;background:#e0e6eb no-repeat center center;background-size:cover;text-align:center;font-weight:600;text-transform:uppercase;color:#fff;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sw-avatar .sw-icon{color:#189eff}.sw-avatar.sw-avatar__square{border-radius:4px}