.sw-select-selection-list{display:flex;flex-wrap:wrap;list-style:none;width:calc(100% - 30px)}.sw-select-selection-list .sw-select-selection-list__item-holder{max-width:220px;line-height:0;margin:8px 6px 0 0}.sw-select-selection-list button.sw-select-selection-list__load-more-button{padding:8px 12px;margin:0 6px 0 0;color:#189eff;font-size:12px;line-height:14px;border-radius:2px;height:unset;border-color:#d1d9e0}.sw-select-selection-list .sw-select-selection-list__input{display:inline-block;min-width:200px;padding:12px 16px 12px 8px}.sw-select-selection-list .sw-select-selection-list__input::-moz-placeholder{color:#94a6b7}.sw-select-selection-list .sw-select-selection-list__input:-ms-input-placeholder{color:#94a6b7}.sw-select-selection-list .sw-select-selection-list__input::placeholder{color:#94a6b7}.sw-field--medium .sw-select-selection-list .sw-select-selection-list__item-holder .sw-label{margin:4px 6px 0 0}.sw-field--medium .sw-select-selection-list input{padding:4px 16px 8px 8px}.sw-field--medium .sw-select-selection-list .sw-select-selection-list__load-more-button{padding:4px 12px;margin:4px 6px 0 0}.sw-field--small .sw-select-selection-list .sw-select-selection-list__item-holder .sw-label{margin:4px 6px 0 0}.sw-field--small .sw-select-selection-list input{padding:2px 16px 4px 8px}.sw-field--small .sw-select-selection-list .sw-select-selection-list__load-more-button{padding:0 8px;margin:-1px 6px 0 0}