.sw-select-result-list,.sw-select-result-list-popover{position:absolute;top:0;left:0;right:0;bottom:0;overflow:hidden}.sw-select-result-list-popover .sw-popover__wrapper{width:100%}.sw-select-result-list__content{width:100%;max-height:250px;overflow-x:hidden;overflow-y:auto;border:1px solid #f0f2f5;box-shadow:0 3px 6px 0 #d1d9e0;background-color:#fff;font-size:14px;font-family:"Source Sans Pro","Helvetica Neue","Helvetica","Arial",sans-serif;padding:8px;border-radius:4px}.sw-select-result-list__content .sw-select-result-list__item-list{list-style:none}.sw-select-result-list__content .sw-select-result-list__empty{padding:10px 16px}.sw-select-result-list__content_empty{opacity:0;min-height:293px;height:293px}.sw-popover__wrapper.--placement-bottom-outside.sw-select-result-list-popover-wrapper{transform:translate(0, calc(-100% - 48px))}