.sw-pagination{min-height:64px;padding:15px 20px;background:#fff;text-align:center;color:#52667a;position:relative;border-top:1px solid #d1d9e0}.sw-pagination .sw-pagination__list{display:inline-block}.sw-pagination .sw-pagination__list-item{display:inline-block;list-style:none;margin:0}.sw-pagination .sw-pagination__list-button,.sw-pagination .sw-pagination__page-button,.sw-pagination .sw-pagination__list-separator{padding:8px 12px;border:0 none;color:#52667a;background:none;cursor:pointer;outline:none}.sw-pagination .sw-pagination__list-button:hover,.sw-pagination .sw-pagination__page-button:hover,.sw-pagination .sw-pagination__list-separator:hover{color:#189eff}.sw-pagination .sw-pagination__page-button[disabled=disabled]{cursor:default}.sw-pagination .sw-pagination__page-button[disabled=disabled]:hover{color:#52667a}.sw-pagination .sw-pagination__list-separator{cursor:default}.sw-pagination .sw-pagination__list-separator:hover{color:#52667a}.sw-pagination .sw-pagination__list-button{border-radius:3px}.sw-pagination .sw-pagination__list-button.is-active{background:#189eff;color:#fff}.sw-pagination .sw-pagination__per-page{display:inline-block;font-size:14px;line-height:16px;position:absolute;right:0;margin:0 20px}@media screen and (max-width: 900px){.sw-pagination .sw-pagination__per-page{display:none}}.sw-pagination .sw-pagination__per-page .sw-field.sw-field--select{grid-template-columns:120px auto}