.sw-card-view{position:absolute;width:100%;height:100%}.sw-card-view.has--sidebar{display:grid;grid-template-columns:1fr auto;grid-template-rows:1fr;justify-content:end;overflow:hidden}.sw-card-view.has--sidebar .sw-card-view__content{height:initial}.sw-card-view .sw-card-view__content{width:100%;height:100%;padding:40px;overflow-x:hidden;overflow-y:auto;-webkit-overflow-scrolling:touch}@media screen and (max-width: 1360px){.sw-card-view .sw-card-view__content{padding:15px}.sw-card-view .sw-card-view__content .sw-tabs--small{max-width:770px}.sw-card-view .sw-card-view__content .sw-card{max-width:770px}.sw-card-view .sw-card-view__content .sw-card--large{max-width:100%}}